import React from 'react'
import {useState,useEffect} from 'react'
import {Container,Title,CopyCom} from '../common'
import {injectIntl,FormattedMessage} from 'gatsby-plugin-intl'
import { navigate } from 'gatsby'
import config from '../../libs/config'
import contractAbi from '../../libs/abi'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import Skeleton from '../skeleton'
import {PulseLoader,BeatLoader,BounceLoader} from 'react-spinners'
import {toast} from 'react-toastify'
import BigNumber from 'bignumber.js'
const web4 = new Web3(config.provider);
const contract = new web4.eth.Contract(contractAbi,config.contractAddress);
const reg = /^[0-9]+$/;
const Login = () => {
    const [userInputId,setUserInputId] = useState('');
    const [send,setSend] = useState(false);
    const [getUp,setGetUp] = useState(false);
    const [pass,setPass] = useState(false);
    const [userInputPass,setUserInputPass] = useState('');
    useEffect(()=>{
    },[null])
    const handleLoginManuallyPass = () => {
        console.log(userInputPass);
        console.log(config.pass)
        console.log(userInputPass == config.pass)
        setGetUp(true);
        if (!send){
            if (userInputPass !== config.pass){
                setGetUp(false);
                return toast.error("Wrong password.")
            } else {
                Cookies.set('user',userInputId);
                navigate('/db/dashboard');
                setGetUp(false);
            }
        }
    }
    const handleOneClickLogin =async () => {
        if (window.ethereum && !send){
            setSend(true);
            window.web3 = new Web3(window.ethereum);
            try {
                const acc = await window.ethereum.enable();
                if (acc && acc[0]){
                    const address = await contract.methods.us(acc[0]).call();
                    if (address.x){
                        Cookies.set('user',acc[0]);
                        navigate('/db/dashboard/');
                        setGetUp(false);
                    } else {
                        toast.error("Your ethereum address not found in contract.")
                        setGetUp(false);
                    }
                } else {
                    toast.error('Can not access your ethereum address');
                }
                setSend(false);
            } catch(error){
                setSend(false);
            }
        } else {
            setSend(false);
        }
    }
    const handleLoginManually = async () => {
        console.log('click')
        if (!getUp){
            setGetUp(true);
            if (web4.utils.isAddress(userInputId)){
                const address = await contract.methods.us(userInputId).call();
                if (address.x){
                    console.log(address.a.toString())
                    if (address.a.toString() == 1 || address.a.toString() == 2 || address.a.toString() == 3 || address.a.toString() == 4){
                        setPass(true);
                        setGetUp(false);                        
                    } else {
                        Cookies.set('user',userInputId);
                        navigate('/db/dashboard');
                        setGetUp(false);
                    }
                } else {
                    toast.error("Invalid ethereum address.")
                    setGetUp(false);
                }
            } else {
                toast.error("Invalid ethereum address.");
                setGetUp(false);
            }
        }
    }
    return (
        <div className="lg-350 mx-auto">
            <Title>
                <span className="text-lights-orange">Log </span>
                in
            </Title>
            <div className="font-sans font-semibold text-14 text-lights-bluebold mb-8 text-center">
                <FormattedMessage id="auth.block1.never" defaultMessage="We never ask you to enter your private keys." />
            </div>
            <div className="mb-4 text-center">
                <button className="w-full py-2 text-lights-white font-semibold focus:outline-none bg-lights-bluebold rounded-lg flex items-center justify-center" onClick={handleOneClickLogin}>
                    {!send?<span className="mx-auto"><FormattedMessage id="auth.block1.btn" defaultMessage="LOGIN AUTOMATICALLY" /></span>:
                        <span className="mx-auto"><BounceLoader color="#fff" size={24}/></span>
                    }
                </button>
            </div>
            <div className="text-center mb-4 text-lights-bluebold font-sans font-semibold">
                <FormattedMessage id="auth.block1.or" defaultMessage="or"/>
            </div>
            {pass ? (
                <>
                    <div className="mb-4">
                        <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                            Enter password
                        </div>
                        <input type="password" name="password" className="block w-full px-2 py-2 focus:outline-none border rounded-lg text-lights-bluebold text-14 font-sans font-semibold" value={userInputPass} onChange={(e)=>setUserInputPass(e.target.value)}/>
                    </div>
                    <div className="text-center">
                        <button className="flex items-center justify-center mx-auto px-4 py-2 bg-lights-orange text-lights-bluebold text-14 font-semibold rounded-lg focus:outline-none" onClick={handleLoginManuallyPass} style={{width: '160px'}}>
                        { !getUp?
                            <span className="mx-auto">LOGIN</span>:
                            <span className="mx-auto"><BounceLoader color="#fff" size={24}/></span>
                        }
                        </button>
                    </div>
                </>
            ):(
                <>
                    <div className="mb-4">
                        <div className="font-sans font-semibold italic text-14 text-lights-bluebold mb-2 leading-none">
                            <FormattedMessage id="auth.block1.eth" defaultMessage="Your ETH-address" />
                        </div>
                        <input type="text" name="ethAddress" className="block w-full px-2 py-2 focus:outline-none border rounded-lg text-lights-bluebold text-14 font-sans font-semibold" value={userInputId} onChange={(e)=>setUserInputId(e.target.value)}/>
                    </div>
                    <div className="text-center">
                        <button className="flex items-center justify-center mx-auto px-4 py-2 bg-lights-orange text-lights-bluebold text-14 font-semibold rounded-lg focus:outline-none" onClick={handleLoginManually} style={{width: '160px'}}>
                        { !getUp?
                            <span className="mx-auto"><FormattedMessage id="auth.block1.btn2" defaultMessage="LOGIN MANUALLY"/></span>:
                            <span className="mx-auto"><BounceLoader color="#fff" size={24}/></span>
                        }
                        </button>
                    </div>
                </>
            )}
        </div>
    )
}
const Block1 = ({location}) => {
    return (
        <Container>
            <div className="md:flex lg:w-4/5 lg:mx-auto">
                <div className="md:w-1/2 mb-8 md:mb-0 md:px-4 mx-auto">
                    <Login />                    
                </div>
            </div>
        </Container>
    )
}
export default injectIntl(Block1)